import React, { useState, useEffect } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, ChevronsLeft, ChevronsRight
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'

import useJwt from '@src/auth/jwt/useJwt'
import { formatReadableDate } from '../../helper'
import DataTable from 'react-data-table-component'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { Error, Success, ErrorMessage } from '../../viewhelper'
import { useHistory } from 'react-router-dom'
const TenantComplains = () => {
    const history = useHistory()
    const [tableDataLoading, setTDL] = useState(true)
    const [ComplainData, setComplainData] = useState([])
    const [PageCount, setPageCount] = useState(1)
    const [PageDataCount, setPageDataCount] = useState(5)
    const [ComplainStatus, setComplainStatus] = useState(null)
    const Statuses = [
        { value: null, label: 'All Complains' },
        { value: 3, label: 'Pending' },
        { value: 12, label: 'Need Approval' },
        { value: 7, label: 'Accepted' },
        { value: 10, label: 'Quotation Pending' },
        { value: 11, label: 'Quotation Approved' },
        { value: 13, label: 'Finished' },
        { value: 2, label: 'Completed' },
        { value: 4, label: 'Cancelled' }
    ]

    const getRenterComplainList = (status_id, page, limit) => {
        useJwt.getRenterComplainList({ status_id, page, limit }).then(res => {
            console.log("getRenterComplainList", res.data.payload)
            setComplainData(res.data.payload)
            setTDL(false)
        }).catch(error => {
            console.log("getRenterComplainList", error)
            setTDL(false)
            // Error(error)
        })
    }

    const onchange = (e) => {
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }

    useEffect(() => {
        getRenterComplainList(ComplainStatus, PageCount, PageDataCount)
    }, [])

    // Data table coulumn
    const column = [
        {
            name: 'ID',
            maxWidth: '50px',
            sortable: true,
            selector: 'complain_id'
        },
        {
            name: 'Complain Date',
            minWidth: '150px',
            sortable: true,
            selector: row => formatReadableDate(row.created_at)
        },
        {
            name: 'Complain Type',
            minWidth: '150px',
            sortable: true,
            selector: row => row.complainItems[0].complainType.complain_type_name
        },
        {
            name: 'Priority',
            minWidth: '150px',
            sortable: true,
            selector: row => row.complainItems[0].priority.priority_name
        },
        {
            name: 'Status',
            minWidth: '150px',
            sortable: true,
            selector: row => row.complainItems[0].statusType.status_name
        },
        {
            name: 'Details',
            minWidth: '100px',
            sortable: true,
            selector: row => <Button.Ripple /*disabled*/ color='primary' type="button" size='sm' onClick={e => history.push(`/EstateManager/Complain/${row.complain_id}-${row.complainItems[0].items_id}`)}>
                View
            </Button.Ripple>
        }
    ]
    const handlePagination = (type) => {
        setTDL(true)
        if (type === 'prev') {
            setPageCount(PageCount - 1)
            getRenterComplainList(ComplainStatus, PageCount - 1, PageDataCount)
        } else {
            setPageCount(PageCount + 1)
            getRenterComplainList(ComplainStatus, PageCount + 1, PageDataCount)
        }
    }
    return (
        <>
            <Card>
                <CardHeader className='border-bottom titleSelect'>
                    <CardTitle tag='h4'>Complains</CardTitle>
                    <CardTitle >
                        <Select
                            // ref={branch_idRef}
                            theme={selectThemeColors}
                            className='react-select'
                            classNamePrefix='select'
                            name='branch_id'
                            maxMenuHeight={150}
                            onChange={selected => {
                                setTDL(true)
                                setPageCount(1)
                                setComplainStatus(selected.value)
                                getRenterComplainList(selected.value, 1, PageDataCount)
                            }}
                            options={Statuses}
                            defaultValue={Statuses[0]}
                            // isDisabled={!ukaddress.length}
                            isClearable={false}
                        />
                    </CardTitle>
                </CardHeader>
                <CardBody>
                    <Row>
                        <Col sm="12">
                            <DataTable
                                noHeader
                                highlightOnHover
                                data={ComplainData}
                                columns={column}
                                className='react-dataTable'
                                progressPending={tableDataLoading}
                                sortIcon={<ChevronDown size={10} />}
                                progressComponent={<Spinner color='primary' />}
                                responsive={true}
                            />
                            <div className='text-right'>
                                <Button.Ripple disabled={PageCount === 1} className='mt-1' color='secondary' onClick={e => handlePagination('prev')}>
                                    <ChevronsLeft color='white' size={15} />
                                </Button.Ripple>&nbsp;&nbsp;&nbsp;&nbsp;<Button.Ripple disabled={!ComplainData.length} className='mt-1' color='secondary' onClick={e => handlePagination('next')}>
                                    <ChevronsRight color='white' size={15} />
                                </Button.Ripple>
                            </div>
                        </Col>
                    </Row>
                </CardBody>
            </Card>
        </>
    )
}

export default TenantComplains